import React from "react";
import AcademicAdministratorTab from "./AcademicAdministratorTab";
import { useLocation, useSearchParams } from "react-router-dom";
import AdministratorSubject from "./AdministratorSubject";
import InstituteClassStudentList from "../../../../Department/Institute/Pages/InstituteClassStudentList";
import Attendence from "../ClassTeacher/ClassCatalog/Attendence/Attendence";

const AdministratorClassDetail = ({ carryParentState }) => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      <AcademicAdministratorTab carryParentState={carryParentState} />

      {searchParams.get("n") === "class-attendance" ? (
        <Attendence
          cid={getQuery.state?.cid}
          isDepartment
          className={getQuery.state?.className ?? ""}
        />
      ) : searchParams.get("n") === "class-subject" ? (
        <AdministratorSubject
          carryParentState={carryParentState}
          clsId={getQuery.state?.cid}
          instituteId={getQuery.state?.onClickValue?.institute?._id}
          did={getQuery.state?.onClickValue?.academic_department?._id}
        />
      ) : (
        <InstituteClassStudentList
          cid={getQuery.state?.cid}
          openAs={"SUBJECT_TEACHER"}
          isDepartment
        />
      )}
    </>
  );
};

export default AdministratorClassDetail;
