import React, { lazy, useEffect, useState } from "react";
import style from "../PromoteDepartment.module.css";
import { imageShowUrl } from "../../../../../services/BaseUrl";
import PromoteTabChangeLink from "../PromoteTabChangeLink";
import AcademicAdministatorTabChange from "../../AcademicAdministrator/AcademicAdministatorTabChange";
import CustomSuspense from "../../../../../Custom/SuspenseLoading/CustomSuspense";
import { useOneClassEditDetail } from "../../../../../Department/Institute/Components/DepartmentApi/depart-api";
const CreateClass = lazy(() =>
  import("../../../../../Dashboard/InsDashboard/Modals/Class/CreateClass")
);

const ClassCard = ({ cls, carryParentState, isView, allClassRefetch }) => {
  const [edit, setEdit] = useState("");

  const { classEditDetail, classEditDetailRefetch } = useOneClassEditDetail({
    cid: edit,
    skip: !edit,
  });

  const onMenu = (e, value) => {
    e.preventDefault();
    setEdit(value);
  };
  useEffect(() => {
    if (edit) classEditDetailRefetch();
  }, [edit, classEditDetailRefetch]);

  const onRefetch = () => {
    allClassRefetch();
    classEditDetailRefetch();
  };
  // console.info("carryParentState", carryParentState);
  return (
    <>
      {isView === "ADMINISTRATOR" ? (
        <AcademicAdministatorTabChange
          carryParentState={{
            ...carryParentState,
            cid: cls?._id,
            className: `${cls?.className ?? ""} - ${cls?.classTitle ?? ""}`,
            classTeacher: `${cls?.classTeacher?.staffFirstName ?? ""} ${
              cls?.classTeacher?.staffMiddleName ?? ""
            } ${cls?.classTeacher?.staffLastName ?? ""}`,
          }}
          activeTab={"class"}
        >
          <div className={style.class_container}>
            <div
              className={style.with_search}
              style={{
                justifyContent: "flex-start",
                gap: "1rem",
              }}
            >
              <img
                className={style.oneRowImage}
                src={
                  cls?.classTeacher?.staffProfilePhoto
                    ? `${imageShowUrl}/${cls?.classTeacher?.staffProfilePhoto}`
                    : "/images/member_tab/class/default_avatar.svg"
                }
                alt="staff avatr"
              />
              <div className={style.class_container_text}>
                <h6>
                  {`${cls?.className ?? ""} - ${cls?.classTitle ?? ""}` ?? ""}
                </h6>
                <p>
                  {`${cls?.classTeacher?.staffFirstName ?? ""} ${
                    cls?.classTeacher?.staffMiddleName ?? ""
                  } ${cls?.classTeacher?.staffLastName ?? ""}`}
                </p>
              </div>
            </div>
            <img
              src="/images/three-24-dot-icon.svg"
              alt="menu icon"
              onClick={(e) => onMenu(e, cls?._id)}
              title="Menu"
              style={{
                cursor: "pointer",
                height: "1.5rem",
              }}
            />
          </div>
        </AcademicAdministatorTabChange>
      ) : (
        <PromoteTabChangeLink
          carryParentState={{
            ...carryParentState,
            cid: cls?._id,
            className: `${cls?.className ?? ""} - ${cls?.classTitle ?? ""}`,
            classStatus: cls?.classStatus ?? "",
          }}
          activeTab={"class"}
        >
          <div className={style.class_container}>
            <img
              className={style.oneRowImage}
              src={
                cls?.classTeacher?.staffProfilePhoto
                  ? `${imageShowUrl}/${cls?.classTeacher?.staffProfilePhoto}`
                  : "/images/member_tab/class/default_avatar.svg"
              }
              alt="staff avatr"
            />
            <div className={style.class_container_text}>
              <h6>
                {`${cls?.className ?? ""} - ${cls?.classTitle ?? ""}` ?? ""}
              </h6>
              <p>
                {`${cls?.classTeacher?.staffFirstName ?? ""} ${
                  cls?.classTeacher?.staffMiddleName ?? ""
                } ${cls?.classTeacher?.staffLastName ?? ""}`}
              </p>
            </div>
          </div>
        </PromoteTabChangeLink>
      )}

      <CustomSuspense>
        {edit && (
          <CreateClass
            departmentModal={edit ? true : false}
            hideModal={() => setEdit("")}
            edit={edit}
            setEdit={setEdit}
            editClass={classEditDetail?.classes}
            onRefetch={onRefetch}
            isEditBy="DEPARTMENT_HEAD"
            id={carryParentState?.onClickValue?.institute?._id}
          />
        )}
      </CustomSuspense>
    </>
  );
};

export default ClassCard;
